.navbar__items {
    box-sizing: border-box;
    
    height: 5rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    font-size: 1.1rem;
    background-color: transparent;

    position: relative;
   
}

ul{
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 2.4rem;
    margin-top: 0px;
    overflow: hidden;
    position: relative;
    margin-right: 2rem;
    
}
