.about__container{
    display: grid;
    grid-template-columns: 35% 50%;
    gap:15%;

}
.about__me{
    width:100%;

    border-radius:2rem;
    background: linear-gradient(
        45deg,
        transparent,
        var(--color-primary),
        transparent
    );
    display: grid;
    place-items: center;

}

.about__me-image{
    border-radius: 2rem;
    overflow: hidden;
    transform: rotate(10deg);
    transition:var(--transistion);

}
.about__me-image:hover{
    transform:rotate(0deg);
}
.about__cards{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 1.5rem;

}
.about__card{
    background: var(--color-bg-variant);
    border-radius:1rem;
    border: 1px solid transparent;
    padding: 2rem;
    text-align: center;
    background-image: url('../../assets/polygon-scatter-haikei.svg');
    background-size: cover;
}


.about__icon{
    color:var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: .5rem;
}
.about__card h5{
    font-size:.95rem;
}
.about__card small{
    font-size: .65rem;
    color: var(--color-light);
    margin-top: 1rem;
}

.about__content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light) ;
}

/*mdeida*/
@media screen and (max-width: 1024px) {
    .about__container{
       grid-template-columns: 1fr;
       gap:0;
    }
    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about__content p{
        margin: 1rem 0 1.5rem;
    }
 }
 
 /*mdeida*/
 @media screen and (max-width: 600px) {
    .about__cards{
        grid-template-columns: 1fr 1fr;
        gap:1rem;
     }
     .about__me{
         width: 65%;
         margin: 0rem auto 3rem;
     }
     .about__content p{
         margin: 1rem 0 1.5rem;
     }
     .about__content{
        text-align: center;
     }
     
 }